.InvitationModalInitialStep .back {
  position: absolute;
  left: 30px;
  top: 30px; }

.InvitationModalInitialStep .invitation-modal-option {
  display: flex;
  max-width: 720px;
  border-top: 1px solid;
  border-top-color: var(--center-channel-color-20);
  padding: 32px;
  width: 100%;
  align-items: center;
  cursor: pointer;
  font-size: 16px; }
  .InvitationModalInitialStep .invitation-modal-option:last-child {
    border-bottom: 1px solid;
    border-bottom-color: var(--center-channel-color-20);
    margin-bottom: 28px; }
  .InvitationModalInitialStep .invitation-modal-option h2 {
    margin-top: 0px;
    margin-bottom: 16px;
    font-size: 24px;
    line-height: 1.38; }
  .InvitationModalInitialStep .invitation-modal-option .arrow svg {
    height: 48px;
    width: 48px;
    margin-left: 32px;
    fill: var(--center-channel-color-20); }
