.FullScreenModal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--center-channel-bg);
  z-index: 1030;
  overflow: auto; }
  .FullScreenModal .close-x {
    position: fixed;
    top: 48px;
    right: 48px;
    cursor: pointer; }
    .FullScreenModal .close-x svg {
      fill: var(--center-channel-color-90);
      padding: 4px;
      width: 40px;
      height: 40px;
      border-radius: 4px; }
      .FullScreenModal .close-x svg:hover {
        background: var(--button-bg-10); }
  .FullScreenModal-enter, .FullScreenModal-appear {
    opacity: 0; }
  .FullScreenModal-enter-active, .FullScreenModal-appear-active {
    opacity: 1;
    transition: opacity 100ms; }
  .FullScreenModal-exit {
    opacity: 1; }
  .FullScreenModal-exit-active {
    opacity: 0;
    transition: opacity 100ms; }
