.InvitationModalMembersStep {
  max-width: 720px;
  width: 100%; }
  .InvitationModalMembersStep h2 {
    margin-bottom: 12px;
    margin-top: 0px;
    font-size: 20px; }
    .InvitationModalMembersStep h2 span {
      font-weight: 400; }
  .InvitationModalMembersStep .share-link {
    max-width: 720px;
    width: 100%; }
    .InvitationModalMembersStep .share-link .share-link-input-block {
      display: flex; }
    .InvitationModalMembersStep .share-link .share-link-input {
      flex-grow: 1;
      height: 48px;
      background: var(--center-channel-color-05);
      border-top: 1px solid var(--center-channel-color-20);
      border-left: 1px solid var(--center-channel-color-20);
      border-bottom: 1px solid var(--center-channel-color-20);
      border-right: 0;
      border-radius: 4px 0 0 4px;
      padding: 12px 14px;
      font-size: 16px;
      color: var(--center-channel-color-50); }
    .InvitationModalMembersStep .share-link .share-link-input-button {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-grow: 0;
      border: 1px solid;
      border-color: var(--button-bg);
      color: var(--button-bg);
      height: 48px;
      width: 180px;
      border-radius: 0 4px 4px 0;
      background: var(--center-channel-bg);
      font-size: 16px;
      font-weight: 600; }
      .InvitationModalMembersStep .share-link .share-link-input-button:hover {
        background: var(--button-bg-05); }
      .InvitationModalMembersStep .share-link .share-link-input-button:active {
        background: var(--button-bg-10); }
      .InvitationModalMembersStep .share-link .share-link-input-button span {
        display: inline-block;
        margin-right: 9px;
        height: 24px; }
      .InvitationModalMembersStep .share-link .share-link-input-button svg {
        fill: var(--button-bg); }
    .InvitationModalMembersStep .share-link .help-text {
      margin-top: 8px;
      color: var(--center-channel-color-60); }
  .InvitationModalMembersStep .search-and-add {
    max-width: 720px;
    width: 100%;
    margin-bottom: 28px; }
    .InvitationModalMembersStep .search-and-add .search-and-add-input {
      height: 48px;
      background: white;
      border: 1px solid rgba(0, 0, 0, 0.3);
      border-radius: 4px;
      padding: 12px 14px;
      width: 100%; }
    .InvitationModalMembersStep .search-and-add .help-text {
      margin-top: 8px;
      color: var(--center-channel-color-60); }
  .InvitationModalMembersStep .invitation-modal-or {
    position: relative;
    max-width: 720px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center; }
    .InvitationModalMembersStep .invitation-modal-or hr {
      max-width: 720px;
      width: calc(100% - 40px);
      margin: 45px 0;
      border-color: var(--center-channel-color-10); }
    .InvitationModalMembersStep .invitation-modal-or div {
      text-align: center;
      display: inline-block;
      position: absolute;
      background: var(--center-channel-bg);
      padding: 30px 12px;
      top: 0;
      color: var(--center-channel-color-20);
      font-size: 20px; }
  .InvitationModalMembersStep .invite-members {
    text-align: right;
    margin-bottom: 28px; }
    .InvitationModalMembersStep .invite-members button {
      height: 48px;
      padding: 13px 26px;
      font-size: 16px;
      font-weight: 600;
      width: 180px;
      border-radius: 4px;
      background: var(--button-bg);
      color: var(--button-color); }
      .InvitationModalMembersStep .invite-members button:focus, .InvitationModalMembersStep .invite-members button:active, .InvitationModalMembersStep .invite-members button:focus:active {
        outline: none;
        border-color: transparent; }
      .InvitationModalMembersStep .invite-members button.btn-inactive {
        background: var(--center-channel-color-10);
        color: var(--center-channel-color-50); }
