.InvitationModal .InvitationModalConfirmStep h1 {
  margin-bottom: 10px; }

.InvitationModalConfirmStep {
  display: block;
  width: 720px; }
  @media screen and (max-width: 768px) {
    .InvitationModalConfirmStep {
      width: 100%; } }
  .InvitationModalConfirmStep .subtitle {
    margin-bottom: 40px;
    margin-top: 0px;
    font-size: 16px;
    text-align: center;
    font-weight: 400; }
    .InvitationModalConfirmStep .subtitle strong {
      font-weight: 600; }
  .InvitationModalConfirmStep .invitation-modal-confirm-not-sent h2 {
    margin-top: 32px;
    margin-bottom: 13px;
    font-size: 20px;
    font-weight: 400; }
  .InvitationModalConfirmStep .invitation-modal-confirm-not-sent .InvitationModalConfirmStepTable .table-header {
    height: 40px;
    font-size: 14px;
    border-bottom: 1px solid;
    border-top: 1px solid;
    border-color: var(--center-channel-color-10);
    background: rgba(238, 179, 0, 0.2);
    color: var(--center-channel-color); }
  .InvitationModalConfirmStep .invitation-modal-confirm-sent h2 {
    font-size: 20px;
    margin-bottom: 13px;
    font-weight: 400; }
  .InvitationModalConfirmStep .invitation-modal-confirm-sent .InvitationModalConfirmStepTable .table-header {
    height: 40px;
    font-size: 14px;
    border-bottom: 1px solid;
    border-top: 1px solid;
    border-color: var(--center-channel-color-10);
    background: rgba(0, 131, 89, 0.2);
    color: var(--center-channel-color); }
    .InvitationModalConfirmStep .invitation-modal-confirm-sent .InvitationModalConfirmStepTable .table-header svg {
      fill: var(--center-channel-color);
      height: 18px;
      width: 18px; }
  .InvitationModalConfirmStep .confirm-done {
    margin-top: 32px;
    text-align: right;
    margin-bottom: 28px; }
    .InvitationModalConfirmStep .confirm-done button {
      height: 48px;
      padding: 13px 26px;
      min-width: 120px;
      font-size: 16px;
      font-weight: 600;
      background: var(--button-bg);
      color: var(--button-color); }
      .InvitationModalConfirmStep .confirm-done button:focus, .InvitationModalConfirmStep .confirm-done button:active, .InvitationModalConfirmStep .confirm-done button:focus:active {
        outline: none;
        border-color: transparent; }
